.video-item{
    display:inline-grid;
    width:30%;
    min-height:400px;
    overflow:hidden;
    margin:2.5px;
    margin-bottom:45px;
}
.video-item img{
    width:100%;
}
.video-item .video-content{
    padding:20px;
    font-family: 'Fox Bebas';
    background:#e7e7e7;
}
.video-item h2{
    font-size:15px;
    margin:0px;
    color:#72a709;
    text-align: center;
}
.video-item a{
    text-decoration: none;
}
.video-item p{
    font-family: Hero;
    font-size:13px;
    text-align: center;
}