.article{
    height:170px;
    position:relative;
    padding-left:170px;
    margin-bottom:20px;
    font-family: 'Fox Bebas';
}
.article .article-img{
    position: absolute;
    top:0px;
    left:0px;
    width:170px;
    height:170px;
    background:#0d0f01;
    display:flex;
    justify-content: center;
}
.article .article-img img{
    width:100%;
}
.article a{
    text-decoration: none;
}
.article h2{
    color:#000;
    text-indent:20px;
    line-height:50px;
    height:50px;
    margin:0px;
}
.article h2:hover{
    text-decoration: underline;
}
.article label{
    color:slategray;
    padding-left:20px;
}
.article p{
    padding-left:20px;
}
.article .tags{
    padding-left:20px;
    padding-top:5px;
}
.article-tag{
    padding:15px;
    padding-top:0px;
    padding-bottom: 0px;
    height:40px;
    display: inline-block;
    background:#d3ff02;
    border-right:1px solid #000;
    cursor:pointer;
    line-height:45px;
}
.article-tag:hover{
    background:#b7ff0e;
}
.article .tags > .article-tag:nth-last-of-type(1){
    border-right:0px;
}