.topbar{
    position:fixed;
    top:0px;
    left:0px;
    right:0px;
    height:70px;
    z-index:300;
}
.topbar.scrolled{
    background:#0d0f01;
}
.topbar .links-left , .topbar .links-right{
    display:inline-block;
    width:40%;
    height:70px;
    text-align: center;
    vertical-align: top;
}
.topbar .links-left a , .topbar .links-right a{
    color:#fff;
    font-family: 'Fox Bebas';
    line-height:70px;
    padding-left:15px;
    padding-right:15px;
    text-decoration: none;
    transition:color 0.2s linear;
    text-shadow: 0px 0px 4px transparent;
}
.topbar .links-left a:hover , .topbar .links-right a:hover{
    color:#d3ff02;
    text-shadow: 0px 0px 4px #d9ff40;
}
.topbar .logo{
    width:19%;
    display: inline-block;
    text-align: center;
    vertical-align: top;
}
.topbar .logo img{
    height:70px;
    margin:0px;
}