@media screen and (min-width:1200px){
    .theatre .video-player{
        position:fixed;top:70px;left:0px;right:400px;
        bottom:0px;
        background:#111;
        overflow: hidden;
    }
    .theatre .video-desc{
        position: fixed;
        top:70px;
        right:0px;
        width:400px;
        bottom:0px;background:#111;
        padding:20px;
        color:#fff;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-weight: lighter;
        line-height:30px;
    }
    .video-desc h2{
        margin:0px;
        color:#d3ff02;
        font-family: 'Fox Bebas';
    }
    .video-desc p{
        margin:0px;
        font-size:13px;
    }
}